:root{
    --color-white: #fff;
    --color-background: #111;
    --color-footer: #000;
    --color-red: #C52626;
    --color-hover: #E90E0E;
    --color-border: #9C9C9C;
    --color-slide-border: rgb(173, 173, 173);
    --color-background-image: linear-gradient(rgba(63, 63, 63, 0.1), rgba(63, 63, 63, 0.1));
    --list-style-type: none;
    --text-align: center;
    --align-items: center;
    --text-decoration: none;
    --justify-content: center;
    --display: flex;
}

/*Typography*/

html{
    font-size: 62.5%;
}

h1, h2, h3, h4{
    font-family: 'Montserrat', sans-serif;
}

h1{
    font-size: 5rem;
}

h2{
    font-size: 3.4rem;
}

h3{
    font-size: 2rem;
}

h4{
    font-size: 1.8rem;
}

p, li, a{
    font-size: 1.6rem;
    font-family: 'Noto Sans', sans-serif;
}

a{
    text-decoration: var(--text-decoration);
    list-style-type: var(--list-style-type);
    color: var(--color-white);
}

*,
*::before, 
*::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth ;
}

/*Container*/

.container{
    margin: 0 auto;
    max-width: 1240px;
}

.container-background{
    background: var(--color-background);
}

/*Separator*/

.separator{
    display: var(--display);
}

.separator-bar{
    background: var(--color-red) ;
}


/* Header Separator*/

.header-separator{
    justify-content: var(--justify-content);
}

.header-separator-bar{
    width: 6.24rem;
    height: 0.16rem;
}

/*Block Separator*/

.block-separator-bar{
    width: 7.1rem;
    height: 0.24rem;
}

/*Body Separator*/

.body-separator{
    justify-content: var(--justify-content);
}

.body-separator-bar{
    width: 9rem;
    height: 0.24rem;
}

/* Main Separator*/

.main-separator{
    display: var(--display);
    justify-content: var(--justify-content);
}

.main-separator-bar{
   height: 1px;
   width: 100%;
   background: var(--color-white);
}

/* Button */

.button{
    background: var(--color-red);
    padding: 1.6rem 4rem;
    border: 1px solid var(--color-red);
    border-radius: 30px;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.4rem;
}

.button:hover{
    background: var(--color-hover);
    transition: 0.4s ease;
}

@media screen and (min-width: 625px) and (max-width: 820px){
    .header-button{
      padding: 1.5rem 3.5rem;
      font-size: 1.3rem;
  }
}

@media screen and (max-width: 625px) {
    .header-button{
        padding: 1.4rem 3rem;
        font-size: 1.2rem;
    }
}

/*Nav*/

.nav{
  background: var(--color-background);
  display: var(--display);
  justify-content: space-between;
  align-items: var(--align-items);
  padding: 1.55rem 2rem;
}

.nav-icons{
    color: var(--color-white);
    font-size: 1.8rem;
    cursor: pointer;
    padding: 0 0.45rem;
}

.nav-color:hover{
    color: var(--color-red);
    transition: 0.5s;
}

.nav-links{
    padding: 2.82rem 2rem;
}

.nav-icons-body{
    padding-left: 7.5rem;
    display: block;
}

.nav-logo{
    width: 19.6rem;
    height: 4.7rem;
    cursor: pointer;
}

.nav-links-body{
    display: block;
}

.nav-menu{
    padding: 1rem;
    border: 1px solid var(--color-red);
    border-radius: 1px;
    background: var(--color-red);
    cursor: pointer;
    width: 4rem;
}

.menu-bar,
.nav-menu:after,
.nav-menu::before{
   content: '';
   display: block;
   width: 100%;
   height: 1.5px;
   background: var(--color-white);
   margin: 4px 0;
   transition: 0.4s;
}

.nav-menu.menuActive::before{
   transform: rotate(-45deg) translate(-4px, 4px);
}

.nav-menu.menuActive .menu-bar{
    opacity: 0;
}

.nav-menu.menuActive::after{
    transform: rotate(45deg) translate(-4px, -4px);
 }

@media screen and (min-width: 940px){
    .nav-menu{
        display: none;
    }
}

@media screen and (max-width: 940px){
    .nav{
     display: block;
     position: absolute;
     width: 100%;  
    }

    .nav-menu{
        position: absolute;
        top: 19px;
        right: 37px;
        display: block;
    }

    .nav-links-body{
        display: none;
        flex-direction: column;
    }

    .nav-links{
        display: var(--display);
        padding: 2rem;
        border-color: var(--color-border);
        border-style: solid;
    }

    .nav-links-top,
    .nav-links-middle{
        border-width: 1px 0 0 0;
    }

    .nav-links-top{
        margin-top: 2.75rem;
    }

    .nav-links-bottom{
        border-width: 1px 0 1px 0;
    }

    .nav-icons-body{
        display: none;
        padding: 2.4rem 2rem; 
    }

    .nav-icons-body.navActive,
    .nav-links-body.navActive{
        display: var(--display);
    }

    .nav.navMenuActive{
        height: 100%;
    }

    .body.navBodyActive{
        overflow-y: hidden;
    }

    .body.navLocationActive{
        overflow-y: visible;
    }
}

