/*Equipment Block*/

.eqp-text{
    color: var(--color-white);
}

.eqp-header{
    text-transform: uppercase;
}

.eqp-slide-text-block{
    margin: 0 7rem 0 7rem ;
    background-image: var(--color-background-image),  url(../img/Heavy-Weights.jpg);
    background-size: cover;
    padding-top: 37rem;
    background-position: inherit;
}

.eqp-text-paragraph-block,
.eqp-header-block{
   display: var(--display);
   justify-content: var(--justify-content);
}

.slide-bar{
    height: 7.64rem;
    position: relative;
    transition: bottom 0.5s ease-out;
    z-index: 10;
    bottom: -77px;
    background-color: var(--color-red);
    cursor: pointer;
}

.eqp-header-block{
   margin-top: 4rem;
}

.eqp-text-paragraph-block{
    margin-bottom: 5rem;
}

.eqp-text-paragraph{
    font-weight: 700;
    font-size: 1.8rem;
    text-align: center;
    line-height: 1.6;
    margin-bottom: 2.3rem;
}

.eqp-separator{
    margin: 2.4rem 0 4.7rem 0;
}

.eqp-block-separator{
    padding-top: 6.4rem;
}

.eqp-text-block{
    margin: 0 10rem 0 10rem;
}

.eqp-slide-hover-block{
  display: var(--display);
  background: transparent;
  align-items: var(--align-items);
  height: 7.64rem;
  padding: 1.9rem 5rem 1.9rem 5rem;
  border-style: solid;
  border-color: var(--color-slide-border);
  cursor: pointer;
}

.eqp-slide-header{
   color: var(--color-white);
   text-transform: uppercase;
   font-size: 3rem;
   font-weight: 700;
   white-space: nowrap;
   margin-right: 7.5rem;
   min-width: 32.329rem;
   z-index: 200;
}

.eqp-slide-text-paragraph{
    color: var(--color-white);
    user-select: none;
    z-index: 500;
}

.eqp-slide-hover-1{
    border-width: 0.8px 0 0.8px 0 ;
}

.eqp-slide-hover-2,
.eqp-slide-hover-3{
    border-width: 0 0 0.8px 0;
}

.eqp-slide-hover-4{
    border-width: 0;
}

.eqp-slide-text-paragraph-1,
.eqp-slide-text-paragraph-2,
.eqp-slide-text-paragraph-3,
.eqp-slide-text-paragraph-4{
    color: transparent;
    user-select: none;
    transition: color 0.2s cubic-bezier(.04,0,.03,1);
}

.eqp-slide-text-paragraph-1{
    color: var(--color-white);
}

.slide-img{
    display: none;
}

.eqp-separator-body{
    display: none;
}

